.input {
  width: 100%;
}
.input > input,
.input > select,
.input > textarea {
  margin: 0.5vmax 0;
  padding: 1vmax 4vmax;
  padding-right: 1vmax;
  width: 100%;
  box-sizing: border-box;
  border: 1px solid rgba(0, 0, 0, 0.267);
  border-radius: 4px;
  font: 300 0.9vmax cursive;
  outline: none;
}

.input > select > option {
  font: 400 1.2vmax "Roboto";
}

.input > svg {
  position: absolute;
  transform: translateX(1vmax);
  font-size: 1.6vmax;
  color: rgba(0, 0, 0, 0.687);
}

.input > input[type="submit"] {
  border: none;
  background-color: tomato;
  color: #fff;
  font: 300 0.9vmax "Roboto";
  width: 100%;
  padding: 0.8vmax;
  cursor: pointer;
  transition: all 0.5s;
  border-radius: 4px;
  outline: none;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.219);
}

.input[type="submit"]:hover {
  background-color: rgb(179, 66, 46);
}

.file {
  flex-direction: column;
}

.input > input[type="file"] {
  display: flex;
  padding: 0;
}

.input > input[type="file"]::file-selector-button {
  cursor: pointer;
  width: 100%;
  z-index: 2;
  height: 5vh;
  border: none;
  margin: 0;
  font: 400 0.8vmax cursive;
  transition: all 0.5s;
  padding: 0 1vmax;
  color: rgba(0, 0, 0, 0.623);
  background-color: #fff;
}

.input > input[type="file"]::file-selector-button:hover {
  background-color: rgb(230, 225, 225);
}
.input > #imagePreview {
  height: 5vmax;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow-x: auto;
}
.input > #imagePreview > img {
  height: 90%;
  width: 6vmax;
  margin-inline: 5px;
}

@media screen and (max-width: 600px) {
  .input{
    width: 90%;
  }
  .input > input,
  .input > textarea,
  .input > select ,
  .input > input[type="submit"]{
    margin: 0.5vmax 0;
    padding: 2vmax 6vmax;
    width: 100%;
    font: 300 2vmax cursive;
  }

  .input > select > option {
    font: 400 2vmax "Roboto";
  }

  .input > svg {
    transform: translateX(1.5vmax);
    font-size: 3vmax;
    color: rgba(0, 0, 0, 0.687);
  }

  .btn {
    padding: 1.5vmax 3vmax;
    font: 500 2vmax "Roboto";
  }

  .input > input[type="file"]::file-selector-button {
    height: 6vh;
    font: 400 2vmax cursive;
  }
  .file {
    align-items: flex-start!important;
  }

  .input > #imagePreview {
    height: 9vmax;
    width: 90%;
  }
  .input > #imagePreview > img {
    height: 90%;
    width: 8vmax;
  }
  .input > input[type="submit"] {
  font: 400 2vmax cursive;

  }
}
